@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
.main {
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
}
.main .image-wrapper {
  display: flex;
  justify-content: center;
}
.main .image-wrapper img {
  width: 100%;
  max-width: 505px;
  object-fit: cover;
}
.main .button {
  background: #E4C1AE;
  color: #ffffff;
  width: 128px;
  height: 48px;
  gap: 10px;
  padding: 16px 40px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  border: none;
  border-radius: 32px;
}
.main .description {
  margin-top: 30px;
  color: #121212;
  max-width: 380px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main .description .title {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  margin: 0;
}
.main .description .copy {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
}
.main .shadow {
  display: none;
  position: absolute;
  background-color: #121212;
  opacity: 0.5;
  width: 100%;
  height: 150%;
}
.main .shadow.active {
  display: block;
}
.main .popup-content.active {
  transform: translate(-50%, -50%) scale(1);
}
.main .popup-content {
  background: #ffffff;
  position: absolute;
  width: 80%;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  padding: 40px 0;
  transition: 200ms ease-in-out;
}
.main .popup-content h2 {
  font-size: 42px;
  font-weight: 700;
  line-height: 120%;
}
.main .popup-content p {
  font-size: 16px;
  line-height: 150%;
}
.main .popup-content p span {
  font-weight: 600;
}
.main .popup-content .button {
  display: none;
}
.main .popup-content .button.active {
  display: block;
}
.main .popup-content .close {
  position: absolute;
  right: 15px;
  top: 10px;
  width: 12px;
  height: 12px;
}
.main .popup-content .close:before, .main .popup-content .close:after {
  position: absolute;
  left: 15px;
  content: " ";
  height: 12px;
  width: 2px;
  background-color: #333;
}
.main .popup-content .close:before {
  transform: rotate(45deg);
}
.main .popup-content .close:after {
  transform: rotate(-45deg);
}

@media (min-width: 769px) {
  .main {
    margin-top: 40px;
    flex-direction: row;
  }
  .main .description {
    padding-left: 40px;
    text-align: left;
    align-items: flex-start;
  }
  .main .popup-content {
    width: 60%;
    text-align: left;
  }
  .main .popup-content h2, .main .popup-content p {
    padding-left: 20%;
  }
}